<template>
  <div class="query-update">
    <el-form :model="queryForm" :rules="rules" ref="queryForm" size="small">
      <el-form-item label="名称" :label-width="formLabelWidth" prop="queryName">
        <el-input v-model="queryForm.queryName" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="所属分类" prop="queryType" :label-width="formLabelWidth">
        <el-select v-model="queryForm.queryType" style="width:100%">
          <el-option label="基本信息" value="2"></el-option>
          <el-option label="职务信息" value="1"></el-option>
        </el-select>
      </el-form-item>
       <el-form-item label="UI内容" :label-width="formLabelWidth" prop="queryName">
        <el-input v-model="queryForm.queryName" type="textarea" :rows="4" auto-complete="off"></el-input>
      </el-form-item>
       <el-form-item label="字典项" :label-width="formLabelWidth" prop="queryName">
        <el-input v-model="queryForm.queryName" type="textarea" :rows="4" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="启用状态" :label-width="formLabelWidth" prop="queryStatus">
        <el-radio-group v-model="queryForm.queryStatus">
          <el-radio label="0">启用</el-radio>
          <el-radio label="1">禁用</el-radio>
        </el-radio-group>
      </el-form-item>
       <el-form-item label="发布位置" :label-width="formLabelWidth" prop="queryStatus">
           <el-checkbox-group v-model="checkedList"  @change="handleCheckedChange">
              <el-checkbox v-for="item in checkList" :label="item" :key="item">{{item}}</el-checkbox>
          </el-checkbox-group>
      </el-form-item>
   
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button type="primary" size="small" @click="saveUserManage(queryForm)">保存</el-button>
      <el-button size="small" type="danger" @click="resetForm('queryForm')">重置</el-button>
    </div>
  </div>
</template>
<script>
 const options = ['常用条件查询', '组合条件查询', '干部培训查询', '全程纪实查询'];
export default {
  name: "queryUpdate",
  props: ["isEdit", "currentRow"],
  components: {
    
  },
  data() {
    return {
      rules: {
        //表单校验
        queryCode: [{ required: true, message: "请输入登录名", tigger: "change" },
         { min: 3, max:20, message: '长度在 3 到 20个字符', trigger: 'blur' }],
        queryName: [{ required: true, message: "请输入用户名", tigger: "change" },
         { min: 3, max:20, message: '长度在 3 到 20个字符', trigger: 'blur' }],
       
      },
      queryForm: {
        orgId: "",
        queryId: "",
        queryCode: "",
        queryName: "",
        queryStatus: "",
        queryEmail: "",
        queryPhone: "",
        queryType: "",
      },
      formLabelWidth: "110px",
      flag: false, //组织机构树不要初始值
      checkList: options,
      checkedList:[]
    };
  },
  created() {
    if (this.isEdit) {
      this.queryForm.orgId = this.currentRow.orgId
      this.queryForm.queryId = this.currentRow.queryId;
      this.queryForm.queryCode = this.currentRow.queryCode;
      this.queryForm.queryName = this.currentRow.queryName;
      this.queryForm.queryStatus = this.currentRow.queryStatus;
      this.queryForm.queryType = this.currentRow.queryType;
      this.queryForm.queryEmail = this.currentRow.queryEmail;
      this.queryForm.queryPhone = this.currentRow.queryPhone;
    }
  },
  methods: {
      handleCheckedChange(value) {

      },
    saveUserManage(queryForm) {
      this.$refs["queryForm"].validate(valid => {
        if (valid) {
          //用户添加保存
          let _this = this;
          let queryUrl ="";
          if (_this.currentRow.queryId) {
            queryUrl = _this.API.updateSysPowerquery;
          } else {
            queryUrl = _this.API.insertSysPowerquery;
          }
          _this
            .$http({
              url: _this.$http.adornUrl(queryUrl),
              method: "post",
              data: _this.$http.adornData(queryForm),
            })
            .then(({ data }) => {
              if (data.code === 0) {
                _this.$message.success("操作成功");
                this.$emit("updateCallback", true);
              } else {
                _this.$message.error("操作失败，请重新尝试");
              }
            })
            .catch(function(error) {});
        } else {
          return false;
        }
      });
    },
    //重置表单
    resetForm(refname) {
      //重置
      this.$nextTick(() => {
        if (this.$refs[refname] !== undefined) {
          this.$refs[refname].resetFields();
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.query-update{
  >>> .el-cascader{
    width:100%;
}
}
</style>


